

import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Toast from 'vux/src/components/toast/index.vue'

import tabBar from '../../components/TabBar'
import searchBar from '../../components/SearchBar';
import tools from '../../lib/tools.js';
import BScroll from 'better-scroll';
export default {
name:'provideGoods',
components:{Flexbox, FlexboxItem,Toast,tabBar,searchBar},
data(){
return {
services:[],
showToast:false,
toastMsg:'',
pickerSortArr:[{itemId:'1',itemName:'健身塑型'},{itemId:'2',itemName:'营养调理'},{itemId:'3',itemName:'外用'},{itemId:'4',itemName:'智能设备'},{itemId:'5',itemName:'其他'}],
pickerContentArr:[],
searchObj:{},
pageNumber:1,
pageSum:0,
times: 0,
loading: false,
loadable:false,
pullUpObj:{
type:0,
text:'松开立即加载'
},// 0 初始化 1可以加载 2最后一页
}
},
mounted(){
var vm = this;
this.init();
},
methods:{
init(){
var vm = this;
let data = {currentPage:this.pageNumber,showCount:10,type:1};
if(Object.keys(this.searchObj).length>0){
for(let key in this.searchObj){
data[key] = this.searchObj[key];
}
}
this.$http.axios({method:'post',url:'/goodsInfo/getGoodsInfoArray',data:data}).then((result) => {

let data = result.data;
if(data.successful && (data.status==200)){
if(data.items && data.items.length==0){
vm.showToast = true;
vm.toastMsg = '未查询到对应的数据';
if(vm.pullUpObj.type == 0){
vm.services = [];
}
return ;
}
this.pageSum = data.pagesCount;
this.loading = false;
this.loadable = (this.pageSum>1);
if(this.pullUpObj.type==0){
vm.pullUpObj.type =1;
vm.services = data.items;
}else{
vm.services = [...vm.services,...data.items];
}
}else{
this.loading = false;
this.loadable = false;
console.log(data.resultCode);
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
}
this.toastMsg = '';
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '查询失败';
});
},
search(value){
console.log('-=-=',value);
this.searchObj = {goodsName:value.value,goodsType:value.type!="全部"?value.type:''};
this.pageNumber = 1;
this.pageSum = 0;
this.times = 0;
this.$set(this.pullUpObj,'type',0);

this.init();
},
nextPage(){
if(!(this.pageSum==this.times)){
this.times += 1;
this.pageNumber += 1;
console.log('nextPage');
this.init();
}else{
this.pullUpObj.type = 2;
this.pullUpObj.text = '已经到底了'
}
},
reLoad(){
this.pullUpObj = {
type:0,
text:'松开立即加载'
};
this.times = 0;
this.pageNumber = 1;
this.init();
},
goTo(id){
window.location.href = '/m/equipmentIndex.html?goodId='+id + '&isBuy=1&=_' +(new Date().getTime());
}
}
}



