export default {
    name:'searchBar',
    props:{
        pickerSort:{
            type: Array,
            default:[],
            required: true
        },
        pickerContent:{
            type: Array,
            default:[],
            required: true
        },
        // checkedPickerSort:{
        //     type: String,
        //     default:'',
        //     required: false
        // },
        // checkedPickerContent:{
        //     type: String,
        //     default:'',
        //     required: false
        // }
        type:{
            type: String,
            default:'',
            required: false
        }
    },
    data(){
        return {
            checkedPickerSort:'',
            checkedPickerSortText:'全部',
            checkedPickerContent:'',
            showPicker:false,
            searchValue:''
        }
    },
    methods:{
        showPickerClick(){
            this.showPicker = !this.showPicker;
        },
        clickPickerSort(item){
            this.checkedPickerSort = item.itemId || '全部';
            this.checkedPickerSortText = item.itemName;
            this.$emit('listenSearch',{type:this.checkedPickerSort,value:this.searchValue});
            this.showPicker = false;
        },
        clickPickerContent(id){
            this.checkedPickerContent = id;
            console.log(this.checkedPickerContent);
        },
        onSubmit(e){
            let theEvent = e || window.event;
            console.log(theEvent);
            this.searchValue += '';
            // if(!this.searchValue){
            //     return false;
            // }
            theEvent.preventDefault();
            theEvent.target.blur();
            let emitObj = {};
            emitObj = {type:this.checkedPickerSort,value:this.searchValue};
            this.$emit('listenSearch',emitObj);
            this.showPicker = false;
            return false;
        }
    },
}



